.input-page {
  padding: 2rem;
  position: relative;
}

.hamburger {
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: 1.8rem;
  cursor: pointer;
  user-select: none;
}

.menu {
  position: absolute;
  top: 45px;
  left: 10px;
  background-color: white;
  border: 1px solid #ccc;
  padding: 10px;
  z-index: 10;
  box-shadow: 0px 2px 5px rgba(0,0,0,0.2);
}

.menu button {
  background-color: #e74c3c;
  color: white;
  border: none;
  padding: 6px 12px;
  font-size: 1rem;
  cursor: pointer;
}

.menu button:hover {
  background-color: #c0392b;
}
.vacancy-button {
  background-color: #4caf50;
  color: white;
  padding: 10px 20px;
  margin-top: 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.vacancy-button:hover {
  background-color: #45a049;
}
